import Vue from 'vue'
import App from './App.vue'

const Login = { template: '<dev>Login page</dev>' }

const routes = {
    '/': App,
    '/login': Login
}

new Vue({
    el: '#app',
    computed: {
        Vuecomponent() {
            return routes[Window.location.pathname] || { template: '<dev> Page not found </dev>' }
        }
    },
    render(h) {
        return h(this.Vuecomponent)
    }
})


